<template>
    <div>
        <head-top webActive="mine"></head-top>
        <div class="bgGray" id="unIndex">
            <div class="container">
                <div class="ucBg">
                    <side-bar></side-bar>
                    <div class="ucPanel ucP_Box">
                        <div class="ucTitNav ofh">
                            <span>地址管理</span>
                            <el-button type="primary" @click="addAddress()" icon="el-icon-edit" plain>添加新地址</el-button>
                        </div>
                        <el-table class="invTable"
                                  :data="tableData"
                                  style="width: 100%">
                            <el-table-column
                                    label="设为默认"
                                    prop="default"
                            >
                                <template slot-scope="scope">
                                    <el-switch
                                            @change = "setDefault(scope.row.add_id,scope.row.is_default)"
                                            :disabled="scope.row.is_default"
                                            v-model="scope.row.is_default">
                                    </el-switch>
                                </template>
                            </el-table-column>
                            <el-table-column
                                    label="收货人"
                                    prop="name"
                            >
                            </el-table-column>
                            <el-table-column
                                    label="地址"
                                    prop="address">
                            </el-table-column>
                            <el-table-column
                                    label="联系电话"
                                    prop="phone">
                            </el-table-column>
                            <el-table-column label="操作" width="150">
                                <template slot-scope="scope">
                                    <el-row>
                                        <el-button size="small" icon="el-icon-edit" @click="editAddress(scope.row.add_id)"></el-button>
                                        <el-button size="small" icon="el-icon-delete" @click="delAddress(scope.row.add_id)"></el-button>
                                    </el-row>
                                </template>
                            </el-table-column>
                        </el-table>
                        <!-- 弹框 -->
                        <el-dialog title="收货地址" :visible.sync="dialogFormVisible">
                            <el-form :model="form" :rules="rules" ref="form" label-width="90px">
                                <el-form-item label="所在地区" prop="place">
                                    <el-input style="display: none;" @focus="selArea = true" type="text" placeholder="省份、城市、区" v-model="form.province+' '+form.city+' '+form.area" autocomplete="off"></el-input>
                                    <v-distpicker class="placeInput" @selected="onSelected" :province="form.province" :city="form.city" :area="form.area"></v-distpicker>
<!--                                    <div class="selPlace" v-show="selArea">-->
<!--                                        <v-distpicker type="mobile" class="placeInput" @selected="onSelected"></v-distpicker>-->
<!--                                    </div>-->
                                </el-form-item>
                                <el-form-item label="详细地址" prop="address_detail" >
                                    <el-input type="textarea" v-model="form.address_detail" placeholder="详细地址，如街道，楼盘号等"></el-input>
                                </el-form-item>
                                <el-form-item label="收货人" prop="name" >
                                    <el-input v-model="form.name"></el-input>
                                </el-form-item>
                                <el-form-item label="手机号码" prop="mobile" >
                                    <el-input v-model.number="form.mobile"></el-input>
                                </el-form-item>
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="dialogFormVisible = false">取 消</el-button>
                                <el-button type="primary" @click="submitForm('form')">确 定</el-button>
                            </div>
                        </el-dialog>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import sideBar from '../../../components/usercenter/sideBar';
    import {mapState} from 'vuex'
    import {address,addAddress,defaultAddress,editUserAddressShow,editUserAddress,delAddress} from '../../../service/api'
    import VDistpicker from 'v-distpicker'
    import headTop from '../../../components/header/headTop';
    export default {
        name: 'addressPage',
        data(){
            return{
                dialogFormVisible: false,
                tableData: [],
                submitType:0,//0:添加地址；1：编辑修改地址
                form: {
                    add_id:'',
                    place:'',//province+city+area
                    address_detail: '',
                    mobile:'',
                    name: '',
                    province:'',
                    city:'',
                    area:''
                },
                selArea:false,//地址选中弹出
                rules:{
                    place: [
                        { required: true, message: '请选择所在地区'},
                    ],
                    address_detail: [
                        { required: true, type:"string", message: '请填写详细地址',trigger: 'blur'},
                    ],
                    name: [
                        { required: true,type:"string", message: '请填写收货人',trigger: 'blur'},
                    ],
                    mobile:[
                        { required: true, message: '请填写手机号码'},
                        { type: 'number', message: '手机号必须为数字值'}
                    ]
                }
            }
        },
        components:{
            sideBar,
            VDistpicker,
            headTop
        },
        mounted(){
            this.initData();
        },
        computed:{
            ...mapState([
                'login','addressIndex'
            ])
        },
        methods:{
            async initData(){
                // 获取列表
                var res = await address();
                //this.login = res.status==12007?false:true;
                if(res){
                    if(res.status != 12007){
                        this.tableData = await address();
                    }else{
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        });
                    }
                }
            },
            handleChange(value){
                console.log(value);
            },
            addAddress(){
                this.form.province ='';
                this.form.city = '';
                this.form.area = '';

                 if(!this.login){
                     this.$message({
                         message: '请先登录',
                         type: 'warning'
                     });
                 }else{
                     this.submitType = 0;
                     if(this.tableData.length>=6){
                         this.$message({
                             message: '地址已达到上限，不能超过6条哦',
                             type: 'warning'
                         });

                     }else{
                         this.dialogFormVisible = true;
                     }
                 }
            },
            onSelected(data) {
                var _this = this;
                _this.form.province = data.province.value;
                _this.form.city = data.city.value;
                _this.form.area = data.area.value;
                _this.form.place = data.province.value+ data.city.value + data.area.value;
                console.log(data);
                _this.selArea = false;
            },
            submitForm(formName) {
                this.$refs[formName].validate(async (valid) => {
                    if (valid) {
                        console.log(this.submitType);
                        console.log(this.form);
                        if(this.submitType==0){//0:添加地址；1：编辑修改地址
                            var res = await addAddress(
                                this.form.address_detail,
                                this.form.mobile,
                                this.form.name,
                                this.form.province,
                                this.form.city,
                                this.form.area
                            );

                        }else{
                            var res = await editUserAddress(
                                this.form.add_id,
                                this.form.address_detail,
                                this.form.mobile,
                                this.form.name,
                                this.form.province,
                                this.form.city,
                                this.form.area
                            );
                        }

                        if(res){
                            this.$message({
                                message: res.msg,
                                type: res.status==12000?'success':'warning'
                            });
                            if(res.status == 12000){
                                this.tableData = await address();
                                this.dialogFormVisible = false;
                            }
                        }

                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            async editAddress(add_id){
                 this.submitType = 1;
                 var res = await editUserAddressShow(
                     add_id
                 );
                 this.form.add_id = res.address_id;
                 this.form.place = res.province +' '+res.city+' '+res.district;
                 this.form.address_detail = res.address_detail;
                 this.form.mobile = parseInt(res.mobile);
                 this.form.name = res.name;
                 this.form.province = res.province;
                 this.form.city = res.city;
                 this.form.area = res.district;
                 this.dialogFormVisible = true;
            },
            async delAddress(add_id){
                 console.log(add_id);
                this.$confirm('确定要删除该地址?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'info'
                }).then(async () => {
                    var res = await delAddress(
                        add_id
                    );
                    console.log("add_id:"+add_id);
                    if(res){
                        this.$message({
                            message: res.msg,
                            type: res.status==12000?'success':'warning'
                        });
                        if(res.status == 12000){
                            this.tableData = await address();
                        }
                    }
                }).catch(async () => {
                    //this.tableData = await address();
                });
            },
            setDefault(add_id,is_default){
                 console.log(is_default);
                 if(is_default == true){
                     this.$confirm('确定要将此条地址设为默认地址?', '', {
                         confirmButtonText: '确定',
                         cancelButtonText: '取消',
                         type: 'info'
                     }).then(async () => {
                         var res = await defaultAddress(
                             add_id
                         );
                         if(res){
                             console.log(res);
                             this.$message({
                                 message: res.msg,
                                 type: res.status==12000?'success':'warning'
                             });
                             if(res.status == 12000){
                                 this.tableData = await address();
                             }
                         }
                     }).catch(async () => {
                         this.tableData = await address();
                     });
                 }

            }
        },
        created(){

        },

    }

</script>

<style lang="scss" scoped>
    @import '../../../style/mixin';
    /*地址管理*/
    .ucTitNav{
        display:flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
    .ucTitNav span{
        font-size: 16px;
        font-weight: bold;
        color: #333;
        padding-left: 10px;
        border-left: 5px solid #0bb8b2;
        margin:5px;
        line-height: 30px;
    }
    .selPlace{
        width:100%;
        height: 300px;
        z-index:1000;
        box-shadow: 0 0 5px 5px #ddd;
        overflow-y: visible;
        overflow-x:auto;
    }

</style>



